import BinaryTree from './binaryTree';
import helpers from './helpers';

function findBestAdjustTree(images, tolerance, imageWidth, imageHeight) {
  const sortedImages = images.sort((a, b) =>
    a.aspectRatio <= b.aspectRatio ? 1 : -1
  );
  const collageArray = [];
  for (let i = 0; i < 100; i += 1) {
    const imgTree = new BinaryTree({ cut: helpers.getCut() });
    imgTree.generateCollageTree(sortedImages, imageWidth / imageHeight);

    collageArray.push(imgTree.adjust(1 - tolerance));
  }

  collageArray.sort((a, b) => {
    const aspR = imageWidth / imageHeight;
    const diffA = Math.abs(a[0] - aspR);
    const diffB = Math.abs(b[0] - aspR);
    return diffA - diffB;
  });

  const bAR = collageArray.filter(
    (elem) => elem[0].toFixed(1) === collageArray[0][0].toFixed(1)
  );
  console.log('from bar', bAR);
  let maxScore = 0;
  let maxElem = {};
  bAR.forEach((element) => {
    element[1].addSize(
      1000 * (imageWidth / imageHeight),
      1000,
      imageWidth / imageHeight
    );
    const score = element[1].generateScore();
    // console.log('from bar all matching', score);
    if (score > maxScore) {
      maxScore = score;
      maxElem = element;
    }
  });
  // console.log('from returning', maxElem, maxScore);
  return maxElem;
  // collageArray[0][1].addSize(
  //   1000 * (imageWidth / imageHeight),
  //   1000,
  //   imageWidth / imageHeight
  // );
  // console.log(
  //   'from best adjust tree',
  //   collageArray[0][0],
  //   collageArray[0][1].generateScore()
  // );
  // return collageArray[0];
}

export default { findBestAdjustTree };
