const get = async (url, path, params = {}) => {
  let newUrl = `https://${url}${path}`;
  const options = {
    method: 'GET',
    credentials: 'include',
    headers: new Headers({ 'content-type': 'application/json' }),
  };
  newUrl += `?${new URLSearchParams(params).toString()}`;
  const resp = await fetch(newUrl, options);
  if (!resp.ok) {
    if (resp.status === 401) {
      document.cookie =
        'userInfo=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;';
      return { status: 'unauthenticated' };
    }
    if (resp.status === 403) {
      return { status: 'forbidden' };
    }
  }

  return resp.json();
};

const post = async (url, path, params, body) => {
  let apiUrl = `https://${url}${path}`;
  apiUrl += `?${new URLSearchParams(params).toString()}`;
  const options = {
    method: 'POST',
    credentials: 'include',
    headers: new Headers({ 'content-type': 'application/json' }),
    body,
  };
  return fetch(apiUrl, options).then((response) => {
    if (!response.ok) {
      return { status: 'error' };
    }
    return response.json();
  });
};

export default { get, post };
