import { useState, useEffect } from 'react';
import helpers from './helpers';

export default function Collage({
  dimensionProps,
  imageProps,
  actionProps,
  collageCallback,
  handleRemoveImage,
  options,
  tree,
}) {
  const [collage, setCollage] = useState(null);
  const { images, originalImages, setLoaded } = imageProps;
  const { generate, handleGenerateCallback, printOptions, removeImage } =
    actionProps;
  const { height, width, imageHeight, imageWidth } = dimensionProps;

  const handleCollageImageLoad = () => setLoaded((cnt) => cnt + 1);
  const isEmpty = (t) => Object.keys(t).length === 0;
  const createCollage = () => {
    console.log('started collage loading');
    tree.addSize(imageWidth, imageHeight, tree.root.value.aspectRatio);
    console.log('collage added size');
    tree.addOptions(options);
    console.log('collage added options', images.length);
    if (images.length > 0) {
      tree.addImageData(images);
      console.log('collage added image data');
      const collageElem = tree.DOMDisplay(
        width,
        height,
        removeImage,
        handleRemoveImage,
        handleCollageImageLoad
      );
      console.log('collage added dom display');
      setCollage(collageElem);
      collageCallback(tree);
    }
  };

  useEffect(() => {
    if (!isEmpty(tree)) {
      console.log('collage, tree is not empty');
      createCollage();
    }
  }, []);

  useEffect(() => {
    if (!isEmpty(tree)) {
      console.log('collage, tree is not empty 1');
      createCollage();
    } else {
      setCollage(null);
    }
  }, [tree, options, imageWidth, imageHeight, removeImage, images]);

  const downloadCollage = (canvas, pW, pH) => {
    canvas.toBlob((blob) => {
      const link = document.createElement('a');
      link.download = `collage-${pW}X${pH}-${Date.now()}.png`;
      link.href = URL.createObjectURL(blob);
      link.click();
      URL.revokeObjectURL(link.href);
      link.href = null;
      handleGenerateCallback();
    });
  };

  useEffect(() => {
    if (generate === true) {
      const canvasElement = document.createElement('canvas');
      const { paperWidth, paperHeight } = printOptions;
      canvasElement.width = paperWidth;
      canvasElement.height = paperHeight;
      const scaleFactor = paperWidth / width;
      const lineArray = helpers.wrapText(
        options.text,
        0,
        0,
        paperWidth,
        20 * scaleFactor,
        25 * scaleFactor
      );
      const textHeight = lineArray.length * 25 * scaleFactor;
      const [imgPrintWidth, imgPrintHeight] = helpers.getCollageDimension(
        paperWidth,
        paperHeight - textHeight,
        tree.root.value.aspectRatio
      );
      const canvasTree = tree.copy();
      canvasTree.replaceImages(originalImages);
      canvasTree.addSize(
        imgPrintWidth,
        imgPrintHeight,
        tree.root.value.aspectRatio
      );
      canvasTree.CanvasDisplay(
        paperWidth,
        paperHeight,
        canvasElement,
        scaleFactor
      );

      setTimeout(
        () => downloadCollage(canvasElement, paperWidth, paperHeight),
        '5000'
      );
    }
  }, [generate]);
  return collage;
}
