import { useState, useEffect } from 'react';
import db from 'db';
import treeUtils from 'components/collage/treeUtils';
import BinaryTree from 'components/collage/binaryTree';

export default function useTree(initialValue, loadingCallback = () => {}) {
  const tableName = 'collageTree';
  const [tree, setTree] = useState(initialValue);

  async function setDataFromDB() {
    const rows = await db.collageDB[tableName].toArray();
    if (rows.length > 0) {
      const newTree = new BinaryTree({});
      newTree.fromObject({ root: rows[0].root });
      if ('options' in rows[0].root) {
        loadingCallback((opts) => ({ ...opts, ...rows[0].root.options }));
      }
      setTree(newTree);
    }
  }

  useEffect(() => {
    setDataFromDB();
  }, []);

  const addTreeToDB = async (data) => {
    db.collageDB.collageTree.clear();
    const cleanedData = data.removeImageData();
    await db.collageDB[tableName].put(cleanedData);
  };

  const clear = () => {
    db.collageDB.collageTree.clear();
    setTree({});
  };

  const reAdjust = (imagesArr, tolerance, imageWidth, imageHeight, options) => {
    const [, bestAspectRatioTree] = treeUtils.findBestAdjustTree(
      imagesArr,
      tolerance,
      imageWidth,
      imageHeight
    );
    bestAspectRatioTree.addOptions(options);
    setTree(bestAspectRatioTree);
    addTreeToDB(bestAspectRatioTree);
  };

  return { tree, setTree, addTreeToDB, clear, reAdjust };
}
