import { useState } from 'react';
import useImageCount from 'hooks/collage/imageCount';
import useIndexedDBState from 'hooks/collage/indexedDBState';
import helpers from 'components/collage/helpers';
import db from 'db';

export default function useImages(resizeImageWidth) {
  const [newImgCount, setNewImageCount] = useState(0);
  const [newImagesLoaded, setNewImagesLoaded] = useState(0);
  const [imgCount, setImageCount] = useImageCount('imgCount', 0);
  const [loaded, setLoaded] = useState(0);
  const [images, setImages, addToImagesDB] = useIndexedDBState(
    'images',
    [],
    () => {}
  );
  const [originalImages, setOriginalImages, addToOriginalImagesDB] =
    useIndexedDBState('originalImages', []);

  const removeImageData = (imageArr) => {
    const tempImgArr = [...imageArr];
    return tempImgArr.map((img) => {
      const { src: _, ...rest } = img;
      return rest;
    });
  };

  const handleImageData = (imageDetail, originalImageDetail) => {
    setOriginalImages((arr) => arr.concat([originalImageDetail]));
    setImages((arr) => arr.concat([imageDetail]));
    addToImagesDB(imageDetail);
    addToOriginalImagesDB(originalImageDetail);
    setNewImagesLoaded((cnt) => cnt + 1);
  };

  const handleLoad = (event) => {
    helpers.handleImageLoad(
      event,
      resizeImageWidth,
      setImageCount,
      setNewImageCount,
      handleImageData
    );
  };

  const clear = () => {
    db.collageDB.images.clear();
    setImages([]);
    db.collageDB.originalImages.clear();
    setOriginalImages([]);
    db.collageDB.imgCount.clear();
    setImageCount(0);
  };

  const remove = (title) => {
    const removeIndex = images.findIndex((elem) => elem.title === title);
    const newImagesArr = [
      ...images.slice(0, removeIndex),
      ...images.slice(removeIndex + 1),
    ];
    setImages(newImagesArr);
    setImageCount((cnt) => cnt - 1);
    db.collageDB.images.delete(title);
    db.collageDB.originalImages.delete(title);
    return newImagesArr;
  };

  return {
    addToImagesDB,
    addToOriginalImagesDB,
    clear,
    handleImageData,
    handleLoad,
    images,
    imgCount,
    loaded,
    newImagesLoaded,
    newImgCount,
    originalImages,
    remove,
    setImageCount,
    setImages,
    setLoaded,
    setNewImageCount,
    setNewImagesLoaded,
    setOriginalImages,
    removeImageData,
  };
}
