import PropTypes from 'prop-types';
import BottomNavigation from '@mui/material/BottomNavigation';
import SaveIcon from '@mui/icons-material/Save';
import DownloadIcon from '@mui/icons-material/Download';
import PrintIcon from '@mui/icons-material/Print';
import { alpha } from '@mui/material/styles';
import MBNA from 'components/collage/MobileActionItem';

export default function MobileActions({ actionsRef, onDownload, onSave }) {
  return (
    <BottomNavigation
      ref={actionsRef}
      showLabels
      sx={{ py: 1, fontWeight: 'bold', backgroundColor: alpha('#fff', 0.2) }}
    >
      <MBNA label="Save" icon={<SaveIcon />} handleChange={onSave} />
      <MBNA
        label="Download"
        icon={<DownloadIcon />}
        handleChange={onDownload}
      />
      <MBNA label="Print" icon={<PrintIcon />} handleChange={() => {}} />
    </BottomNavigation>
  );
}

MobileActions.propTypes = {
  actionsRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
  ]).isRequired,
  onDownload: PropTypes.func.isRequired,
  onSave: PropTypes.func.isRequired,
};
