import { useState } from 'react';

export default function useActions() {
  const [open, setOpen] = useState(false);
  const [refresh, setRefresh] = useState(false);
  const [generate, setGenerate] = useState(false);
  const [removeImage, setRemoveImage] = useState(false);
  const [printOptions, setPrintOptions] = useState({
    paperWidth: 0,
    paperHeight: 0,
  });

  const onRefresh = () => setRefresh(true);
  const handleDownload = () => setOpen(true);
  const onDownloadClose = () => setOpen(false);
  const clearDelete = () => setRemoveImage(false);
  const onDelete = () => setRemoveImage((ri) => !ri);

  const onDownloadClick = (w, h) => {
    setPrintOptions({ paperWidth: w, paperHeight: h });
    setGenerate(true);
    setOpen(false);
  };

  const handleGenerateCallback = () => {
    setGenerate(false);
    setPrintOptions({ paperWidth: 0, paperHeight: 0 });
  };

  return {
    clearDelete,
    generate,
    handleDownload,
    handleGenerateCallback,
    onDelete,
    onDownloadClick,
    onDownloadClose,
    onRefresh,
    open,
    printOptions,
    refresh,
    setGenerate,
    setOpen,
    setPrintOptions,
    setRefresh,
    removeImage,
    setRemoveImage,
  };
}
