import helpers from 'api/helpers';

function newCollage(collageID, collageDetails) {
  try {
    const path = '/api/collage/new';
    const params = { collage_id: collageID };
    const authURL = process.env.REACT_APP_AUTH_URL;
    return helpers.post(authURL, path, params, JSON.stringify(collageDetails));
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
    return error;
  }
}

async function upload(url, body) {
  try {
    const params = { body, method: 'PUT' };
    const resp = await fetch(url, params).then((response) => response);
    return resp;
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
    return error;
  }
}

export default { newCollage, upload };
