import PropTypes from 'prop-types';
import { alpha } from '@mui/material/styles';
import Stack from '@mui/material/Stack';
import PaddingControl from 'components/collage/PaddingControl';
import RadiusControl from 'components/collage/RadiusControl';
import ColorControl from 'components/collage/ColorControl';
import TextControl from 'components/collage/TextControl';

export default function EditControl({ optionProps }) {
  const {
    options,
    handlePaddingChange,
    displayColorPicker,
    handleColorChange,
    handleColorClose,
    handleColorClick,
    handleRadiusChange,
    handleTextChange,
    handlePositionChange,
  } = optionProps;

  return (
    <Stack
      direction="column"
      spacing={4}
      sx={{
        p: 2,
        backgroundColor: alpha('#000', 0.7),
        borderRadius: '10px 10px 10px 10px',
        boxShadow: '0 0 10px inset rgba(255, 255, 255, 0.3)',
      }}
    >
      <PaddingControl
        padding={options.padding}
        handlePaddingChange={handlePaddingChange}
      />
      <RadiusControl
        radius={options.borderRadius}
        handleRadiusChange={handleRadiusChange}
      />
      <ColorControl
        color={options.color}
        displayColorPicker={displayColorPicker}
        handleChange={handleColorChange}
        handleClose={handleColorClose}
        handleClick={handleColorClick}
      />
      <TextControl
        text={options.text}
        topText={options.topText}
        handleTextChange={handleTextChange}
        handlePositionChange={handlePositionChange}
      />
    </Stack>
  );
}

EditControl.propTypes = {
  optionProps: PropTypes.shape({
    options: PropTypes.shape({
      padding: PropTypes.number.isRequired,
      borderRadius: PropTypes.number.isRequired,
      text: PropTypes.string.isRequired,
      topText: PropTypes.bool.isRequired,
      color: PropTypes.shape({
        r: PropTypes.number.isRequired,
        g: PropTypes.number.isRequired,
        b: PropTypes.number.isRequired,
        a: PropTypes.number.isRequired,
      }),
    }).isRequired,
    handlePaddingChange: PropTypes.func.isRequired,
    handleColorClose: PropTypes.func.isRequired,
    handleColorChange: PropTypes.func.isRequired,
    handleColorClick: PropTypes.func.isRequired,
    handleRadiusChange: PropTypes.func.isRequired,
    handleTextChange: PropTypes.func.isRequired,
    handlePositionChange: PropTypes.func.isRequired,
    displayColorPicker: PropTypes.bool.isRequired,
  }).isRequired,
};
