import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import { alpha } from '@mui/material/styles';

import PaddingControl from 'components/collage/PaddingControl';
import RadiusControl from 'components/collage/RadiusControl';
import TextControl from 'components/collage/TextControl';
import { ColorPicker } from 'components/collage/ColorControl';
import MainControls from 'components/collage/MainControls';
import EditOptions from 'components/collage/EditOptions';
import ImageOptions from 'components/collage/ImageOptions';
import ClearCollageDialog from 'components/collage/ClearCollageDialog';

export default function MobileControls({
  optionProps,
  actionProps,
  dimensionProps,
  handleClear,
}) {
  const {
    options,
    handlePaddingChange,
    handleRadiusChange,
    handleColorClose,
    handleColorClick,
    handleColorChange,
    handleTextChange,
    handlePositionChange,
    displayColorPicker,
  } = optionProps;

  const { onRefresh, onDelete, clearDelete } = actionProps;
  const { footerRef, handleImageAddClick } = dimensionProps;

  const [mainOpen, setMainOpen] = useState(true);
  const [value, setValue] = useState('');
  const [editOpen, setEditOpen] = useState(false);
  const [clearOpen, setClearOpen] = useState(false);
  const [editControl, setEditControl] = useState(null);
  const [editOptionsValue, setEditOptionsValue] = useState('');
  const [imageOpen, setImageOpen] = useState(false);
  const [imageValue, setImageValue] = useState('');

  const handleImageOptionsClose = () => {
    setImageOpen(false);
    setImageValue('');
    setMainOpen(true);
    clearDelete();
    setValue('');
  };

  const handleEditOptionsClose = () => {
    setEditOpen(false);
    setEditControl(null);
    setEditOptionsValue('');
    setMainOpen(true);
    setValue('');
  };

  const handleEditOptionsClick = (e, nV) => {
    console.log('handle edit options click');
    setEditOptionsValue(nV);
    if (nV === 'padding') {
      setEditControl(
        <PaddingControl
          padding={options.padding}
          handlePaddingChange={handlePaddingChange}
        />
      );
    } else if (nV === 'border radius') {
      setEditControl(
        <RadiusControl
          radius={options.borderRadius}
          handleRadiusChange={handleRadiusChange}
        />
      );
    } else if (nV === 'color') {
      handleColorClick();
      setEditControl(
        <ColorPicker
          open={displayColorPicker}
          color={options.color}
          handleChange={handleColorChange}
          handleClose={() => {
            setEditControl(null);
            handleColorClose();
          }}
        />
      );
    } else if (nV === 'text') {
      console.log(nV);
      setEditControl(
        <TextControl
          text={options.text}
          handleTextChange={handleTextChange}
          topText={options.topText}
          handlePositionChange={handlePositionChange}
        />
      );
    } else {
      setEditControl(<Typography color="secondary">{nV}</Typography>);
    }
  };

  useEffect(() => {
    if (editOpen && editOptionsValue === 'padding') {
      setEditControl(
        <PaddingControl
          padding={options.padding}
          handlePaddingChange={handlePaddingChange}
        />
      );
    }
  }, [options.padding, editOpen]);
  // }, [options.padding, editOpen, editOptionsValue]);

  useEffect(() => {
    if (editOpen && editOptionsValue === 'border radius') {
      setEditControl(
        <RadiusControl
          radius={options.borderRadius}
          handleRadiusChange={handleRadiusChange}
        />
      );
    }
  }, [options.borderRadius, editOpen]);
  // }, [options.borderRadius, editOpen, editOptionsValue]);

  useEffect(() => {
    if (editOpen && editOptionsValue === 'color') {
      setEditControl(
        <ColorPicker
          open={displayColorPicker}
          color={options.color}
          handleChange={handleColorChange}
          handleClose={handleColorClose}
        />
      );
    }
  }, [options.color, editOpen, displayColorPicker]);
  // }, [options.color, editOpen, editOptionsValue, displayColorPicker]);

  useEffect(() => {
    console.log('text control useEffect');
    if (editOpen && editOptionsValue === 'text') {
      setEditControl(
        <TextControl
          text={options.text}
          handleTextChange={handleTextChange}
          topText={options.topText}
          handlePositionChange={handlePositionChange}
        />
      );
    }
  }, [options.text, editOpen, options.topText]);
  // }, [options.text, editOpen, editOptionsValue, options.topText]);

  const handleImageClose = () => {
    setImageValue('');
    setImageOpen(false);
    setMainOpen(true);
  };

  const handleChange = (event, newValue) => {
    clearDelete();
    handleImageClose();
    handleEditOptionsClose();
    setValue(newValue);
    if (newValue === 'rearrange') {
      onRefresh();
    } else if (newValue === 'photo') {
      setMainOpen(false);
      setImageOpen(true);
    } else if (newValue === 'edit') {
      setMainOpen(false);
      setEditOpen(true);
    } else if (newValue === 'clear') {
      setClearOpen(true);
      //  handleClear();
    } else {
      console.log('functionality for ', newValue, 'not yet implemented');
    }
  };

  const handleImageAdd = () => {
    clearDelete();
    handleImageAddClick();
  };

  const handleClearSubmit = () => {
    handleClear();
    setClearOpen(false);
    setValue('');
  };

  const handleClearCancel = () => {
    setClearOpen(false);
    setValue('');
  };

  console.log('rerendering', editControl);
  return (
    <Paper
      ref={footerRef}
      sx={{
        position: 'fixed',
        bottom: 0,
        left: 0,
        right: 0,
        mt: 1,
        backgroundColor: 'black',
        borderRadius: 0,
      }}
      elevation={4}
    >
      <ClearCollageDialog
        open={clearOpen}
        onSubmit={handleClearSubmit}
        onCancel={handleClearCancel}
      />
      {editControl ? (
        <Box
          sx={{
            backgroundColor: alpha('#fff', 0.15),
            p: 1,
          }}
        >
          {editControl}
        </Box>
      ) : null}
      <ImageOptions
        open={imageOpen}
        value={imageValue}
        handleDelete={onDelete}
        handleAdd={handleImageAdd}
        handleOptionsClose={handleImageOptionsClose}
      />
      <EditOptions
        color={options.color}
        editOptionsValue={editOptionsValue}
        open={editOpen}
        handleOptionsClose={handleEditOptionsClose}
        handleEditOptionsClick={handleEditOptionsClick}
      />
      {mainOpen ? (
        <MainControls value={value} handleChange={handleChange} />
      ) : null}
    </Paper>
  );
}

MobileControls.propTypes = {
  optionProps: PropTypes.shape({
    options: PropTypes.shape({
      padding: PropTypes.number.isRequired,
      borderRadius: PropTypes.number.isRequired,
      text: PropTypes.string.isRequired,
      topText: PropTypes.bool.isRequired,
      color: PropTypes.shape({
        r: PropTypes.number.isRequired,
        g: PropTypes.number.isRequired,
        b: PropTypes.number.isRequired,
        a: PropTypes.number.isRequired,
      }),
    }).isRequired,
    handlePaddingChange: PropTypes.func.isRequired,
    handleRadiusChange: PropTypes.func.isRequired,
    handleColorChange: PropTypes.func.isRequired,
    handleColorClose: PropTypes.func.isRequired,
    handleColorClick: PropTypes.func.isRequired,
    displayColorPicker: PropTypes.bool.isRequired,
    handleTextChange: PropTypes.func.isRequired,
    handlePositionChange: PropTypes.func.isRequired,
  }).isRequired,
  actionProps: PropTypes.shape({
    onRefresh: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    clearDelete: PropTypes.func.isRequired,
  }).isRequired,
  handleClear: PropTypes.func.isRequired,
  dimensionProps: PropTypes.shape({
    handleImageAddClick: PropTypes.func.isRequired,
    footerRef: PropTypes.oneOfType([
      PropTypes.func,
      PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
    ]).isRequired,
  }).isRequired,
};
